* {
  font-family: 'Montserrat', sans-serif;
  margin: 0;
}
.App {
  background-color: #1e262f;
  padding: 60px 80px 180px 80px;
  height: auto;
  width: 100%;
}
.userList {
  background-color: #101317;
  padding: 50px 20px;
  border-radius: 16px;
}
.customContainer {
  padding: 0 20px;
  padding: 10px;
  height: auto;
  margin-bottom: 30px;
  border-radius: 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  background-color: #1e262f;
  color: white;
}
.details {
  width: 300px;
  display: flex;
  justify-content: space-between;
}
.details > img {
  width: 60px;
  height: 60px;
  margin: auto 0px;
  border-radius: 50%;
}
.detailContainer {
  margin: auto 0;
}
.sec {
  font-size: 0.9rem;
  color: #bdbcbe;
}
.progressBar {
  cursor: pointer;
  margin: auto 0;
  width: 90px;
  height: 90px;
}
.bar {
  font-size: 0.9rem;
  margin: 0;
}
.bold {
  font-weight: bold;
  font-size: 1.2rem;
}
.d-flex {
  display: flex;
}
.workout {
  margin: auto 0;
  flex-direction: column;
}
p {
  margin: 0;
}
h2 {
  margin: 0;
}
.performedDate {
  margin-left: 15px;
}
.semibold {
  font-weight: 500;
  font-size: 1.2rem;
}
.customButton {
  cursor: pointer;
  background-color: #101317;
  width: 45px;
  height: 90px;
  border-radius: 10px;
  text-align: center;
  margin: auto 10px;
}
.customButton > img {
  transform: scale(1.5);
  margin-top: 37px;
}
.nutrition {
  margin: auto 0;
  display: flex;
}
.barContent {
  text-align: center;
}
.notification {
  background-color: #36f5c7;
  width: 45px;
  height: 45px;
  border-radius: 8px;
  margin: auto 0;
  text-align: center;
  cursor: pointer;
}
.notification > img {
  margin-top: 12px;
}
.header {
  font-size: 1.2rem;
  font-weight: bold;
  color: white;
  margin-bottom: 30px;
  margin-left: 25px;
  display: flex;
  flex-wrap: wrap;
}
.header > p {
  margin-right: 23vw;
  width: 100px;
  box-sizing: content-box;
}

.header img {
  margin-right: 20px;
}
.increment,
.decrement {
  cursor: pointer;
  width: 40px;
  height: 20px;
  background-color: #101317;
  border-radius: 6px;
  font-size: 1.2rem;
  font-weight: bold;
}
.increment > img,
.decrement > img {
  transform: scale(1.5);
  margin-bottom: 10px;
}
.decrement > img {
  transform: scale(1.5);
  margin-bottom: 12px;
}
.overlay {
  text-align: left;
  padding: 8px 14px;
  left: -110px;
  top: 80px;
  position: absolute;
  border-radius: 8px;
  z-index: 100;
  width: 310px;
  background-color: #333b44;
  color: white;
}
.component {
  background-color: #1b222a;
  margin: 10px auto;
  padding: 10px;
  border-radius: 8px;
}
.progress {
  height: 12px;
  background-color: #101317;
}
.progress-bar {
  border-radius: 10px;
  background-color: #f45c84;
}
.progress-bar > p {
  position: absolute;
}
.warning {
  background-color: #cc3838;
  border-radius: 10px;
  padding: 8px 8px;
}
.content {
  padding: 0px 8px;
}

#walk {
  margin-right: 9.2vw;
}
#workout {
  margin-right: 10.6vw;
}
#walk > img {
  height: 25px;
}

#workout > img {
  width: 35px;
  margin-bottom: 28px;
}

#nutrition > img {
  width: 30px;
  margin-bottom: 26px;
}

@media screen and (max-width: 1270px) {
  .holder {
    margin-right: 20vw;
  }
  #walk {
    margin-right: 6.4vw;
  }
  #workout {
    margin-right: 7.4vw;
  }
}
@media screen and (max-width: 1120px) {
  .holder {
    margin-right: 20vw;
  }
  #walk {
    margin-right: 6.4vw;
  }
  #workout {
    margin-right: 7.4vw;
  }
  .nutrition {
    margin: 50px 0 30px 0;
  }
  .notification {
    margin: 80px 0 30px 0;
  }
  .holder {
    margin-right: 22vw;
  }
  .overlay {
    margin-left: 50px;
  }
}
@media screen and (max-width: 890px) {
  .workoutGroup {
    margin: 45px 0 30px 0;
  }
  .workout {
    margin-top: 30px;
  }
  .App {
    padding: 20px 20px 180px 20px;
  }
  .overlay {
    margin-left: 100px;
  }
}
@media screen and (max-width: 700px) {
  #workout {
    visibility: hidden;
  }
  .header {
    justify-content: space-between;
  }
}
@media screen and (max-width: 567px) {
  #walk {
    visibility: hidden;
  }
  .details {
    margin: 10px auto 20px;
  }
  .workoutGroup {
    margin-top: 10px;
    margin-bottom: 20px;
  }
  .steps {
    margin-top: 10px;
    margin-left: 10px;
  }
  .notification {
    margin-right: 10px;
  }
  .nutrition {
    margin-left: 10px;
  }
  .overlay {
    margin-left: 120px;
  }
}
@media screen and (max-width: 478px) {
  .details {
    flex-direction: column;
    text-align: center;
  }
  .details > img {
    margin: 5px auto;
  }
  .steps,
  .workoutGroup {
    margin: 10px auto;
  }
  .notification,
  .nutrition {
    margin-left: auto;
    margin-right: auto;
  }
}
@media screen and (max-width: 377px) {
  .notification,
  .nutrition {
    margin: 15px auto;
  }
  .overlay {
    margin-left: 80px;
  }
}
